@import "./styles/UI";
/* Import Google Fonts */
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600&family=Prompt:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");

/* Override default style and set padding and margin to nothing */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  // font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 65%;
}

body {
  font-family: Tahoma;
  background-color: rgb(238, 238, 246);
}

main {
  text-align: center;
  padding-top: 50px;
  min-height: 100vh;
}

h1 {
}

/* Make all link text black with no text decoration */
a {
  text-decoration: none;
  color: white;
}

/* No styling on list items */
li {
  list-style-type: none;
}

img {
  width: 100%;
}

/* Target all input fields */
input[type="text"],
input[type="email"],
input[type="password"] {
  color: $secondary-color; /* Change the text color */
}

/* Target placeholder text for all input fields */
input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder {
  color: $secondary-color; /* Change the text color */
}

/* Target text color of textarea */
textarea {
  color: $secondary-color; /* Change the text color */
}

/* Target placeholder text color for textarea */
textarea::placeholder {
  color: $secondary-color; /* Change the text color */
}

