.open {
  background: $primary-color;
  color: white;
  border-top: 4px solid $secondary-color;

  h2 {
    color: $secondary-color;
    font-family: "Monoton";
  }
}

/* pc normal screen */
@media (max-width: 1400px) {
  html {
    font-size: 62.5%;
  }
}

/* Tablet screen */
@media (max-width: 899px) {
  html {
    font-size: 50%;
  }
}

/* Mobile screen */
@media (max-width: 580px) {
  html {
    font-size: 45%;
  }
}
